@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
}

html,
body {
  width: 100%;
  height: 100%;
}

h1,
h2,
h3,
h4 {
  font-weight: 500;
}

.section {
  overflow: hidden;
  width: 100%;
}
.section_first {
  background-color: #d9d9d9;
  height: 77vh;
  display: flex;
  align-items: center;
  position: relative;
}
.section_second {
  background-color: #313540;
}
.section__wrapper {
  width: 91%;
  max-width: 1500px;
  margin: auto;
}

.button {
  padding: 10px 80px;
  background-color: #dd2925;
  color: #fff;
  border-radius: 25px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
}
.button:focus {
  outline: none;
}
.button:active {
  background-color: #f8504d;
}

.fav__btn {
  height: calc(23vh - 80px);
  display: flex;
  justify-content: center;
}
.fav__btn .filmlist__button {
  padding-bottom: 0;
}

.modal {
  position: fixed;
  padding: 15px;
  bottom: 15px;
  right: 20px;
  opacity: 0;
  border-radius: 5px;
  background-color: #566269;
  color: #d9d9d9;
  animation-name: show;
  animation-duration: 10s;
  animation-direction: alternate;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  animation-delay: 10s;
  z-index: -1;
}

@keyframes show {
  0% {
    opacity: 0;
    z-index: 1;
  }
  20%, 80% {
    opacity: 1;
    z-index: 1;
  }
  100% {
    opacity: 0;
  }
}
.rating {
  position: relative;
  top: 3px;
  margin-right: 10px;
}
.rating__wrapper {
  margin-top: auto;
}

.search {
  height: 30px;
  padding: 0 20px;
  font-size: 16px;
  color: #ededed;
  background-color: #414f57;
  border: none;
  border-radius: 16px;
}
.search:focus {
  background-color: #566269;
  outline: none;
}
.search::placeholder {
  color: #c7c7c7;
}
.search_wrapper {
  width: 300px;
}

.header {
  width: 100%;
  background-color: #313540;
  font-size: 30px;
  display: flex;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: visible;
}
.header__link {
  color: #c7c7c7;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
}
.header__logo {
  font-size: 1em;
  color: #ededed;
  font-family: "Montserrat", sans-serif;
}
.header__logo_accent {
  color: #d72323;
  font-family: "Montserrat", sans-serif;
}
.header__controls {
  font-size: 0;
  width: 350px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.header__search_wrapper {
  font-size: 0;
  width: 80%;
}
.header__search {
  width: 100%;
}
.header__login_btn svg {
  fill: #c7c7c7;
  padding: 25px 0;
  margin: 0 5px;
  cursor: pointer;
}
.header__login_btn:hover svg {
  fill: #ededed;
}
.header__login_btn:hover ~ .header__menu_wrapper {
  display: block;
}
.header__profile_avatar {
  padding: 25px 0;
}
.header__profile_avatar span {
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #d6d6d6;
}
.header__profile_avatar:hover ~ .header__menu_wrapper {
  display: block;
}
.header__menu {
  width: 100%;
  background-color: #414f57;
  list-style-type: none;
  font-size: 16px;
  padding: 10px 16px;
  border-radius: 10px;
}
.header__menu_wrapper {
  display: none;
  width: 100px;
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 1;
}
.header__menu_wrapper:hover {
  display: block;
}
.header__menu_arrow {
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 16px solid #414f57;
  position: absolute;
  right: 8px;
  top: -16px;
}
.header__menu_link {
  margin: 3px 0;
}
.header__menu_link:hover a {
  color: #ededed;
}

.slider {
  width: 75%;
  max-width: 1200px;
  margin: auto;
}
.slider *:focus {
  outline: none;
}
.slider__mask {
  position: absolute;
  width: 100%;
  height: 100%;
}
.slider__wrapper .slick-list {
  overflow: visible;
}
.slider__item {
  height: 60vh;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  position: relative;
  background: linear-gradient(90deg, #313540 0%, #3c3b41 80%, rgba(157, 139, 139, 0) 100%);
  border-radius: 20px;
}
.slider__item_mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #313540 0%, #3c3b41 69%, rgba(157, 139, 139, 0) 95%);
  border-radius: 20px;
}
.slider__item_description {
  padding: 40px;
  height: 100%;
  position: relative;
  z-index: 1;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.slider__item img {
  border-radius: 0 20px 20px 0;
  object-fit: cover;
  width: 30%;
}
.slider__title {
  font-size: 40px;
  padding-bottom: 10px;
  line-height: 40px;
}
.slider__subtitle {
  padding-top: 10px;
}

.section__slider {
  height: 77vh;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  width: 100%;
}

.filmlist {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  column-gap: 2rem;
  row-gap: 2rem;
  align-items: stretch;
  padding-bottom: 50px;
}
.filmlist__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 50px;
}
.filmlist__notFound {
  grid-column-start: 1;
  grid-column-end: 6;
  text-align: center;
  color: #b6b6b6;
}
.filmlist .MuiPaper-root {
  margin: 0;
  margin-left: 0;
}

.filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: calc(23vh - 80px);
  color: #d9d9d9;
}
.filter__params {
  display: flex;
  align-items: center;
}
.filter__params > * {
  padding-right: 40px;
}
.filter__params_drawer {
  display: flex;
  flex-direction: column;
  padding: 40px;
}
.filter__params_drawer .filter__dropdown_sort .MuiFormControl-root {
  margin-left: 10px;
}
.filter__params_drawer .MuiFormControl-root {
  margin: 0;
}
.filter__types {
  margin-right: -13px;
}
.filter__types_drawer {
  padding-left: 5px;
}
.filter__dropdown_sort {
  display: flex;
  align-items: center;
  width: 210px;
  margin-right: -13px;
}
.filter__slider {
  display: flex;
}
.filter__slider > span {
  padding-right: 20px;
  padding-top: 2px;
}
.filter__slider > div {
  width: 300px;
}
.filter .MuiPaper-root {
  top: 20px;
}
.filter .MuiFormControlLabel-root {
  margin-left: -9px;
}
.filter__control {
  margin: 20px 0;
}
.filter__btn {
  background-color: #414f57;
  position: relative;
  left: 50%;
  transform: translatex(-50%);
}
.filter__btn:active, .filter__btn:hover {
  background-color: #445b68;
}

.MuiTooltip-tooltip.MuiTooltip-tooltipPlacementBottom {
  color: #d9d9d9;
  margin: 0;
}

.MuiDrawer-paper.MuiDrawer-paperAnchorLeft {
  background: #313540;
  color: #d9d9d9;
  display: flex;
  justify-content: center;
}

.MuiPaper-root {
  margin-left: -10px;
  background-color: #d9d9d9;
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
}

.MuiButtonBase-root {
  display: block !important;
  padding: 8px !important;
}

.MuiSelect-select:focus {
  background-color: transparent;
}

.MuiSelect-root {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
}

.MuiSelect-icon {
  right: 5px;
}

.MuiSvgIcon-root {
  fill: #d9d9d9;
}

.card {
  width: 100%;
}
.card__desc {
  padding: 0 5px;
  display: inline-block;
}
.card__desc::first-letter {
  text-transform: uppercase;
}
.card__favbtn {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.card__favbtn:focus {
  outline: none;
}
.card__favbtn svg {
  position: relative;
  top: 2px;
}
.card__favbtn svg path:first-child {
  fill: #b5b5b6;
}
.card__favbtn svg path:nth-child(2) {
  fill: #909394;
}
.card__favbtn_isFav svg path:first-child {
  fill: #ffdc64;
}
.card__favbtn_isFav svg path:nth-child(2) {
  fill: #ffc850;
}
.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card__img_wrapper {
  height: 400px;
}
.card .MuiPaper-root {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.card .MuiCardContent-root:last-child {
  padding-bottom: 16px;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.card .MuiCardContent-root:last-child div:last-child {
  margin-top: auto;
}

.profile__title {
  padding-bottom: 1rem;
  font-size: 1.8rem;
}
.profile__wrapper {
  display: flex;
  justify-content: space-between;
}
.profile__avatar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile__avatar_input {
  display: none;
}
.profile__avatar_btn {
  background-color: #414f57;
  margin-top: 30px;
}
.profile__avatar_btn:active, .profile__avatar_btn:hover {
  background-color: #445b68;
}
.profile__avatar_canvas {
  width: 200px;
  height: 300px;
  background-color: #414f57;
}
.profile__information {
  width: 75%;
  display: flex;
  flex-direction: column;
}
.profile__information_nickname {
  font-size: 1.2rem;
  text-transform: uppercase;
  line-height: 1.2rem;
}
.profile__information_input {
  background-color: rgba(144, 163, 173, 0.5);
  padding: 5px;
  border: none;
  border-bottom: 1px solid #414f57;
  font-size: 1rem;
  border-radius: 5px 5px 0 0;
}
.profile__information_input:focus {
  outline: none;
}
.profile__information_input_title {
  font-weight: 500;
}
.profile__information_input_wrapper {
  display: flex;
  flex-direction: column;
  width: 33.33%;
  padding: 10px 35px;
}
.profile__information_wrapper {
  margin: 0 -35px;
  display: flex;
  flex-wrap: wrap;
}
.profile__information_textarea {
  resize: none;
  background-color: rgba(144, 163, 173, 0.5);
  border: 1px solid #414f57;
  margin-top: 5px;
  padding: 5px;
  font-size: 1rem;
  height: 85px;
}
.profile__information_textarea:focus {
  outline: none;
}
.profile__information_textarea_wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px 0;
}
.profile__information_btn {
  background-color: #414f57;
  padding: 10px;
  width: 210px;
}
.profile__information_btn_wrapper {
  align-self: flex-end;
  margin-top: auto;
}
.profile__information_btn:active, .profile__information_btn:hover {
  background-color: #445b68;
}

.disabled {
  background-color: transparent;
  border-radius: 0;
}

@media (max-width: 1440px) {
  .card__img_wrapper {
    height: 350px;
  }
}
@media (max-width: 1200px) {
  .card__img_wrapper {
    height: 300px;
  }
}
@media (max-width: 1024px) {
  .filmlist {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .filter__slider > div {
    width: 200px;
  }
  .filter__types {
    display: flex;
    flex-direction: column;
  }
  .filter__params > * {
    padding-right: 20px;
  }

  .profile__avatar_canvas {
    position: relative;
    top: calc(50% - 30px);
    transform: translateY(-50%);
  }
  .profile__avatar_btn {
    margin-top: auto;
  }
  .profile__information {
    width: 65%;
  }
  .profile__information_wrapper {
    margin: 0 -15px;
  }
  .profile__information_input_wrapper {
    width: 50%;
    padding: 10px 15px;
  }
}
@media (max-width: 768px) {
  .filmlist {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .slider {
    width: 85%;
  }
  .slider__title {
    font-size: 30px;
    line-height: 30px;
  }
  .slider__params {
    flex-wrap: wrap;
  }
  .slider__params > * {
    padding-right: 0;
  }
  .slider__item {
    height: 70vh;
    margin: 0px 10px;
    justify-content: center;
  }
  .slider__item img {
    width: auto;
    border-radius: 20px;
  }
  .slider__item_mask {
    position: absolute;
    background: rgba(49, 53, 64, 0.9);
  }
  .slider__item_description {
    position: absolute;
  }

  .header__controls {
    justify-content: flex-end;
  }

  .profile__wrapper {
    flex-direction: column;
  }
  .profile__title {
    padding: 20px 0;
  }
  .profile__avatar {
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
  }
  .profile__avatar_canvas {
    position: static;
    top: 0;
    transform: translateY(0);
  }
  .profile__avatar_btn {
    margin-top: 0;
  }
  .profile__information {
    padding: 20px 0;
    width: 100%;
  }
  .profile__information_input_wrapper {
    width: 50%;
  }
  .profile__information_btn_wrapper {
    align-self: center;
  }

  .section_first {
    height: auto;
  }
}
@media (max-width: 500px) {
  .filmlist {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 425px) {
  .MuiDrawer-paper.MuiDrawer-paperAnchorLeft {
    width: 300px;
  }

  .card__img_wrapper {
    height: 450px;
  }

  .section_second .section__wrapper {
    width: 80%;
  }

  .filmlist {
    grid-template-columns: 1fr;
  }

  .slider__item img {
    width: 100%;
  }
  .slider__item_description {
    padding: 15px;
  }

  .profile__avatar {
    flex-direction: column;
  }
  .profile__avatar_btn {
    margin-top: 20px;
  }
  .profile__information_input_wrapper {
    width: 100%;
  }
}
@media (min-width: 1800px) {
  .slider__mask {
    z-index: 1;
    background: linear-gradient(90deg, #d9d9d9 5%, rgba(217, 217, 217, 0) 20%, rgba(217, 217, 217, 0) 80%, #d9d9d9 95%);
  }
}
@media (min-width: 2200px) {
  .slider__mask {
    background: linear-gradient(90deg, #d9d9d9 10%, rgba(217, 217, 217, 0) 25%, rgba(217, 217, 217, 0) 75%, #d9d9d9 90%);
  }
}